  /* Basic styling for the thank you page */
.thankyou-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f4f4f9;
  padding: 1rem;
  text-align: center;
}

.thankyou-content {
  background: white;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 500px;
  width: 100%;
}

.thankyou-heading {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #4a90e2;
}

.thankyou-message {
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
  color: #333;
}

.back-button {
  padding: 0.75rem 2rem;
  font-size: 1rem;
  background-color: #4a90e2;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.back-button:hover {
  background-color: #357abf;
}

/* Responsive Design */
@media (max-width: 768px) {
  .thankyou-heading {
    font-size: 2rem;
  }

  .thankyou-message {
    font-size: 1rem;
  }

  .back-button {
    width: 100%;
    padding: 0.75rem;
  }
}

@media (max-width: 480px) {
  .thankyou-heading {
    font-size: 1.8rem;
  }

  .thankyou-message {
    font-size: 0.9rem;
  }

  .back-button {
    width: 100%;
  }
}
