body {
  margin: 0;
 padding: 0;

max-height: 100%;

display: flex;
flex-direction: column;
overflow: auto;

 }
 #bg4{
 position: fixed;
  background-repeat: no-repeat;
 background-size:cover;
 background-position: center center;
 animation: changeBg 15s 2s infinite;
 width: 100%;
 z-index: -1;
 min-height: 100vh;
 }
 #root{
 height: 100%;
 }


 @keyframes changeBg{
  0%,100%  {background-image: url("img/bg1.webp");
}
  25% {background-image: url("img/bg2.jpg");
}
  50% {background-image: url("img/bg3.jpg");}
  75% {background-image: url("img/bg4.jpg");}
  /* 100% {background-image: url("https://cdn.pixabay.com/photo/2023/09/09/10/25/mountains-8242910_1280.png");} */
  
}

 @media only screen and (max-width: 500px) {
  @keyframes changeBg{
    0%,100%  {background-image: url("img/bg5.jpg");}
    25% {background-image: url("img/bg6.webp");}
    50% {background-image: url("img/bg7.webp");}
    75% {background-image: url("img/bg8.jpg");}
    /* 100% {background-image: url("https://cdn.pixabay.com/photo/2023/09/09/10/25/mountains-8242910_1280.png");} */
    
  }
 }
 

